import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import MyContext from '../context/MyContext';
import Attributes from './Attributes';

class MiniCartNou extends Component {
  static contextType = MyContext;

  state = {
    pret: 0,
    total: 0,
    variante: {},
  };

  currencyOptions = {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2,
  };

  componentDidMount() {
    if (Object.keys(this.context.cart).length < 1) {
      this.setState({
        total: 0,
        toate: [],
      });
    }

    document.addEventListener('mousedown', this.context.clickOutsideCart);
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.context.clickOutsideCart);
  }

  getPrices = (prices) => {
    let pret;
    prices.map((item) => {
      if (item.currency.label === this.context.currency.label) {
        pret = (
          <div key={item.currency.label}>
            <span>{item.currency.symbol}</span>
            <span>{item.amount.toLocaleString(undefined, this.currencyOptions)}</span>
          </div>
        );
      }
      return pret;
    });
    return pret;
  };

  getTotal = () => {
    const totalAll = [];
    let total = 0;
    let pret = 0;

    for (const [, value] of Object.entries(this.context.cart)) {
      value.product.prices.map((item) => {
        if (item.currency.label === this.context.currency.label) {
          pret = item.amount;
        }
        return pret;
      });
      const totalProdus = pret * value.productAmount;
      totalAll.push(totalProdus);
    }

    total = totalAll.reduce((totalCost, item) => totalCost + item, 0);

    return total.toLocaleString(undefined, this.currencyOptions);
  };

  render() {
    const cart = this.context.cart;
    Object.keys(cart).forEach((k) => (!cart[k] && cart[k] !== undefined) && delete cart[k]);
    const cartKeys = Object.keys(cart || []);

    return (
      <>
        <div id="overlay" className="hide" />
        <div className="container">
          <div className="cart-box hide">
            <div className="cart-title">
              <strong>My Bag</strong>
              ,
              {' '}
              {Object.values(cart).length}
              {' '}
              items
            </div>
            { cartKeys.map((key) => {
              const {
                brand, name, gallery, prices, attributes,
              } = this.context.cart[key].product;
              const { productAmount, id } = this.context.cart[key];

              return (
                <div className="grid-container" key={key}>
                  <div className="grid-item1">
                    {brand}
                    <br />
                    {name}
                  </div>
                  <div className="grid-item2">
                    <button
                      className="active"
                      type="button"
                      onClick={() => this.context.addToCart({
                        id,
                        product: this.context.cart[key].product,
                        productAmount: 1,
                        variants: this.context.cart[key].variants,
                      })}
                    >
                      +
                    </button>
                  </div>
                  <div className="grid-item-img" style={{ backgroundImage: `url(${gallery[0]})` }} alt={name} />
                  <div className="grid-item4">
                    { this.getPrices(prices) }
                  </div>
                  <div className="grid-item5">{productAmount}</div>
                  <div className="grid-item6">
                    <Attributes key={key} attributes={attributes} product={key} />
                  </div>
                  <div className="grid-item7">
                    <button
                      className="active"
                      type="button"
                      onClick={() => this.context.removeFromCart(
                        { id: key },
                      )}
                    >
                      -
                    </button>
                  </div>
                </div>
              );
            })}

            <div className="total">
              <h4>Total</h4>
              <h4>
                {this.context.currency.symbol}
                {this.getTotal()}
              </h4>
            </div>
            <div className="box-action">
              <Link to="/cart">
                <button
                  type="button"
                  className="bag"
                  onClick={this.context.handleCartClick}
                >
                  view bag
                </button>
              </Link>
              <button
                type="button"
                onClick={this.context.checkout}
                className="out"
              >
                check out
              </button>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default MiniCartNou;
