import React, { Component } from 'react';
import { Outlet } from 'react-router-dom';
import GetCategory from '../utils/GetCategory';
import MyContext from '../context/MyContext';
import GetCurrency from '../utils/GetCurrency';
import logo from '../assets/imgs/Brand-icon.png';
import emptycart from '../assets/imgs/empty-cart.png';

class Nav extends Component {
  static contextType = MyContext;

  render() {
    return (
      <div className="container">
        <header id="head">
          <nav>
            <ul>
              <GetCategory />
            </ul>
          </nav>
          <Outlet />
          <div className="logo">
            <img src={logo} alt="Brand icon" />
          </div>
          <div className="action">
            <ul>
              <li>
                <GetCurrency />
              </li>
              <li className="nav-cart" onClick={this.context.handleCartClick}>
                <span>
                  <img src={emptycart} alt="empty-cart" />
                </span>
                {
                    Object.keys(this.context.cart).length > 0 && (
                    <span className="incart">
                      { Object.keys(this.context.cart).length }
                    </span>
                    )
                }
              </li>
            </ul>
          </div>
        </header>
      </div>
    );
  }
}

export default Nav;
