import React, { Component } from 'react';
import { Markup } from 'interweave';
import MyContext from '../context/MyContext';
import CarouselProduct from './CarouselProduct';

class Product extends Component {
  static contextType = MyContext;

  componentDidMount() {
    this.context.initialVariants(this.props.attributes);
  }

  getAttributes = (attributes) => {
    const attr = [];
    attributes.map((item) => {
      attr.push(<div key={item.id}>
                    <h3>{item.name}</h3>
                    <div className="buttons">
                    { item.type === 'text'
                        && item.items.map((elem) => (
                                <button
                                  type="button"
                                  key={elem.id}
                                  onClick={() => this.context.handleVariantChange(item, elem)}
                                  className={this.context.handleSelected(item, elem) && 'selected'}
                                >
                                    {elem.displayValue}
                                </button>
                        ))}
                    {
                        item.type === 'swatch'
                        && item.items.map((elem) => (
                                <button
                                  type="button"
                                  key={elem.id}
                                  aria-label={elem.id}
                                  className={this.context.handleSelected(item, elem) && 'selected swatch'}
                                  onClick={() => this.context.handleVariantChange(item, elem)}
                                  style={{ backgroundColor: elem.value }}
                                />
                        ))
                    }

                    </div>
                </div>);
      return attr;
    });
    return attr;
  };

  getPrices = (prices) => {
    let pret;
    prices.map((item) => {
      if (item.currency.label === this.context.currency.label) {
        pret = (
          <div key={item.currency.label}>
            <span>{item.currency.symbol}</span>
            <span>{item.amount.toLocaleString(undefined, this.currencyOptions)}</span>
          </div>
        );
      }
      return pret;
    });
    return pret;
  };

  render() {
    const {
      id,
      name,
      brand,
      gallery,
      description,
      inStock,
      prices,
      attributes,
    } = this.props;
    const { product } = this.props;

    return (
      <article className="product">
        <h2 className="hide">
          <strong>{brand}</strong>
          {name}
        </h2>
        <CarouselProduct gallery={gallery} />
        <div className="product-info">
          <h1>
            <strong>{brand}</strong>
            <br />
            {name}
          </h1>
          { this.getAttributes(attributes) }
          <h3>Price</h3>
          <h3>
            { this.getPrices(prices) }
          </h3>

          { inStock ? (
                    <button
                      type="button"
                      className="add-cart"
                      onClick={() => this.context.addToCart({
                        id,
                        product,
                        productAmount: 1,
                        variants: this.context.variants,

                      })}
                        >
                        Add to cart
                    </button>
          ) : (
                    <button type="button" className="nostock">Out of stock</button>
          )}

          <div><Markup content={description} /></div>

        </div>
      </article>
    );
  }
}

export default Product;
