import React, { Component } from 'react';
import MyContext from '../context/MyContext';

class Attributes extends Component {
  static contextType = MyContext;

  render() {
    return (
      this.props.attributes.map((item) => (
        <div key={item.id}>
          <h4>{item.name}</h4>
          <div>
            { item.type === 'text'
              && item.items.map((elem) => (
                <button
                  type="button"
                  key={elem.id}
                  onClick={() => this.context
                    .handleVariantChangeCart(this.props.product, item, elem)}
                  className={this.context
                    .handleSelectedCart(this.props.product, item, elem) && 'selected'}
                >
                  {elem.displayValue}
                </button>
              ))}
          </div>
          <div>
            { item.type === 'swatch'
              && item.items.map((elem) => (
                <button
                  type="button"
                  aria-label={elem.id}
                  key={elem.id}
                  className={this.context
                    .handleSelectedCart(this.props.product, item, elem) && 'selected swatch'}
                  onClick={() => this.context
                    .handleVariantChangeCart(this.props.product, item, elem)}
                  style={{ backgroundColor: elem.value }}
                />
              ))}
          </div>
        </div>
      ))
    );
  }
}

export default Attributes;
