import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import MyContext from '../context/MyContext';
import carticon from '../assets/imgs/cart-icon.png';

class CategoryProduct extends Component {
  static contextType = MyContext;

  state = {
    showCart: false,
  };

  handleCartToggle = () => this.setState({
    showCart: !this.state.showCart,
  });

  getPrices = (prices) => {
    let pret;
    prices.map((item) => {
      if (item.currency.label === this.context.currency.label) {
        pret = (
          <div key={item.currency.label}>
            <span>{item.currency.symbol}</span>
            <span>
              {item
                .amount
                .toLocaleString(undefined, this.currencyOptions)}
            </span>
          </div>
        );
      }
      return pret;
    });
    return pret;
  };

  render() {
    const {
      id,
      name,
      brand,
      gallery,
      inStock,
      prices,
      category,
    } = this.props;

    return (

      inStock
        ? (
          <article
            onMouseEnter={this.handleCartToggle}
            onMouseLeave={this.handleCartToggle}
            className={this.state.showCart
              ? 'cart-item'
              : ''}
          >
            <div className="image-details cart-image">
              <Link to={`/products/${category}/${id}`} key={id}>
                <div
                  className="product-image"
                  style={{
                    backgroundImage: `url(${gallery[0]})`,
                  }}
                  alt={name}
                />
              </Link>
              <img
                src={carticon}
                alt="cart"
                role="button"
                tabIndex="0"
                className={this.state.showCart
                  ? 'green-cart'
                  : 'green-cart-hide'}
                onClick={() => {
                  this.context.addToCart({
                    id,
                    product: this.props,
                    productAmount: 1,
                    variants: {},
                  });
                }}
              />
            </div>

            <div className="product-details">
              <div className="product-name">
                <Link to={`/products/${category}/${id}`} key={id}>
                  {brand}
                  {name}
                </Link>
              </div>

              <div className="product-price">
                {this.getPrices(prices)}
              </div>

            </div>
          </article>
        )
        : (
          <article className="no-stock-item">
            <div className="image-details no-stock-image">
              <div
                className="product-image"
                style={{
                  backgroundImage: `url(${gallery[0]})`,
                }}
                alt={name}
              />
              <div className="no-stock">out of stock</div>
            </div>
            <div className="product-details">
              <div className="product-name">
                <Link
                  style={{
                    display: 'block',
                    margin: '1rem 0',
                  }}
                  to={`/products/${category}/${id}`}
                  key={id}
                >
                  {brand}
                  {name}
                </Link>
              </div>

              <div className="product-price">
                {this.getPrices(prices)}
              </div>

            </div>
          </article>
        )
    );
  }
}

export default CategoryProduct;
