import {
  gql,
  ApolloClient,
  InMemoryCache,
} from '@apollo/client';

import React, { Component } from 'react';
import Product from '../components/Product';
import MiniCartNou from '../components/MiniCartNou';
import MyContext from '../context/MyContext';

const client = new ApolloClient({
  // uri: 'http://localhost:4000/',
  uri: '/graphql',
  cache: new InMemoryCache(),
});

class GetProduct extends Component {
  static contextType = MyContext;

  state = {};

  componentDidMount() {
    const { productID } = this.props;
    const ProductQuery = gql`
    query ProductPage($productID: String!) {
      product(id: $productID) {
        name
        brand
        gallery
        description
        inStock
        prices {
          currency {
            label
            symbol
          }
          amount
        }
        attributes {
          id
          name
          type
          items {
            displayValue
            value
            id
          }
        }
      }
    }
  `;

    client.query({
      query: ProductQuery,
      variables: { productID },
    })
      .then((result) => {
        if (result.loading) {
          console.log(result.loading);
        } else {
          console.log(result.data);
        }
        return result.data;
      })
      .then((data) => this.setState({ data }));
  }

  render() {
    const { isCartinVisible } = this.context;
    const { productID } = this.props;

    let sevede;

    if (this.state.data) {
      const {
        name, brand, gallery, description, inStock, prices, attributes,
      } = this.state.data.product;
      sevede = (
        <section className={`cart-on ${isCartinVisible ? 'hidden' : ''}`}>
          <MiniCartNou key={productID} id={productID} />
          <div className="container">
            <Product
              key={productID}
              id={productID}
              name={name}
              brand={brand}
              gallery={gallery}
              description={description}
              inStock={inStock}
              prices={prices}
              attributes={attributes}
              product={this.state.data.product}
            />
          </div>
        </section>
      );
    } else {
      sevede = <></>;
    }
    return (sevede);
  }
}

export default GetProduct;
