import React, { Component } from 'react';
import MyContext from '../context/MyContext';
import CarouselCart from './CarouselCart';
import Attributes from './Attributes';

class Cart extends Component {
  static contextType = MyContext;

  state = {};

  componentDidUpdate() {
    const variants = this.context.cart[this.props.cartKey].variants;
    for (const property in variants) {
      if (Object.prototype.hasOwnProperty.call(this.context.variants, property)) {
        variants[property] = this.context.variants[property];
      }
    }
  }

  getPrices = (prices) => {
    let pret;
    prices.map((item) => {
      if (item.currency.label === this.context.currency.label) {
        pret = (
          <div key={item.currency.label}>
            <span>{item.currency.symbol}</span>
            <span>{item.amount.toLocaleString(undefined, this.currencyOptions)}</span>
          </div>
        );
      }
      return pret;
    });
    return pret;
  };

  render() {
    const {
      brand, name, gallery, prices, attributes,
    } = this.props.cartItem.product;
    const { productAmount, id } = this.props.cartItem;
    return (
      <div className="grid-container">
        <div className="grid-item1">
          <strong>{brand}</strong>
          <br />
          {name}
        </div>
        <div className="grid-item2">
          <button
            type="button"
            onClick={() => this.context.addToCart({
              id,
              product: this.context.cart[this.props.cartKey].product,
              productAmount: 1,
              variants: this.context.cart[this.props.cartKey].variants,
            })}
          >
            +
          </button>
        </div>

        { gallery.length > 1
          ? (
            <div className="grid-item-img">
              <CarouselCart gallery={gallery} />
            </div>
          )
          : (
            <div className="grid-item-img">
              <img src={gallery[0]} alt={name} />
            </div>
          )}

        <div className="grid-item4">
          { this.getPrices(prices) }
        </div>
        <div className="grid-item5">{productAmount}</div>
        <div className="grid-item6">
          <Attributes
            key={this.props.cartKey}
            attributes={attributes}
            product={this.props.cartKey}
          />
        </div>
        <div className="grid-item7">
          <button
            type="button"
            onClick={() => this.context.removeFromCart({
              id: this.props.cartKey,
            })}
          >
            -
          </button>
        </div>
      </div>
    );
  }
}

export default Cart;
