import React, { Component } from 'react';
import MyContext from './MyContext';

class MyProvider extends Component {
  state = {
    currency: {
      label: 'USD',
      symbol: '$',
    },
    showOptionList: false,
    defaultSelectText: '$',
    cart: [],
    isCartinVisible: true,
    variants: {},
    isSelected: false,
  };

  render() {
    return (
      <MyContext.Provider
        value={{
          currency: {
            label: this.state.currency.label,
            symbol: this.state.currency.symbol,
          },

          showOptionList: this.state.showOptionList,
          defaultSelectText: this.state.defaultSelectText,

          handleOptionClick: (e) => {
            const currency = { ...this.state.currency };
            currency.label = e.target.getAttribute('data-label');
            currency.symbol = e.target.getAttribute('data-symbol');
            this.setState({
              currency,
            });
            this.setState({
              showOptionList: false,
              defaultSelectText: `${e.target.getAttribute('data-symbol')}${e.target.getAttribute('data-label')}`,
            });
          },

          handleClickOutside: (e) => {
            if (
              !e.target.classList.contains('custom-select-option')
              && !e.target.classList.contains('selected-text')
            ) {
              this.setState({
                showOptionList: false,
              });
            }
          },

          handleListDisplay: () => {
            this.setState((prevState) => ({
              showOptionList: !prevState.showOptionList,
            }));
          },

          isCartinVisible: this.state.isCartinVisible,

          clickOutsideCart: (e) => {
            if (e.target.classList.value === 'hide' || e.target.classList.value === 'navigare') {
              this.setState({
                isCartinVisible: true,
              });
            }
          },

          handleCartClick: () => {
            this.setState((prevState) => ({ isCartinVisible: !prevState.isCartinVisible }));
          },

          cart: this.state.cart,

          addToCart: (cartItem) => {
            const { cart } = this.state;
            let selectedOptionsObj;
            if (Object.keys(cartItem.variants).length === 0) {
              selectedOptionsObj = cartItem.product.attributes.reduce((prev, curr) => {
                prev[curr.name] = curr.items[0].value;
                return prev;
              }, {});
              cartItem.variants = selectedOptionsObj;
              this.state.variants = selectedOptionsObj;
            }

            const haveSameVariants = function haveSameVariants(obj1, obj2) {
              const obj1Length = Object.keys(obj1).length;
              const obj2Length = Object.keys(obj2).length;

              if (obj1Length === obj2Length) {
                return Object.keys(obj1).every(
                  (key) => Object.prototype.hasOwnProperty.call(obj2, key)
                   && obj2[key] === obj1[key],
                );
              }
              return true;
            };

            const incos = cart.filter((item) => {
              if (item.id === cartItem.id) {
                return true;
              }
              return false;
            });

            const different = incos.map((elem) => {
              let distinct;
              if (haveSameVariants(elem.variants, cartItem.variants)) {
                elem.productAmount += cartItem.productAmount;
                distinct = 0;
              } else {
                distinct = 1;
              }
              return distinct;
            });

            const nouincos1 = different.some((item) => item !== 1);
            if (!nouincos1) {
              cart.push(cartItem);
            }

            const nouincos = cart.some((item) => item.id === cartItem.id);
            if (!nouincos) {
              cart.push(cartItem);
            }

            localStorage.setItem('cart', JSON.stringify(cart));
            this.setState({ cart });
          },

          removeFromCart: (cartItem) => {
            let { cart } = this.state;
            if (cart[cartItem.id].productAmount > 1) {
              cart[cartItem.id].productAmount = cart[cartItem.id].productAmount - 1;
            } else {
              delete cart[cartItem.id];
            }
            const isAtLeastOnenotNull = cart.some((i) => i !== null);
            let { variants } = this.state;
            let { isCartinVisible } = this.state;
            if (!isAtLeastOnenotNull) {
              cart = [];
              variants = {};
              localStorage.removeItem('cart');
              isCartinVisible = true;
            }
            localStorage.setItem('cart', JSON.stringify(cart));
            this.setState({ cart, isCartinVisible, variants });
          },

          checkout: () => {
            const cart = [];
            const variants = {};
            localStorage.removeItem('cart');
            const isCartinVisible = true;
            this.setState({ cart, isCartinVisible, variants });
          },

          variants: this.state.variants,
          isSelected: this.state.isSelected,

          initialVariants: (attr) => {
            const selectedOptionsObj = attr.reduce((prev, curr) => {
              prev[curr.name] = curr.items[0].value;
              return prev;
            }, {});
            this.setState({
              variants: selectedOptionsObj,
            });
          },

          handleVariantChange: (attr, target) => {
            const variants = { ...this.state.variants };
            for (const property of Object.entries(this.state.variants)) {
              if (property[0] === attr.id) {
                variants[property[0]] = target.value;
                this.setState({ variants });
              }
            }
          },

          handleVariantChangeCart: (productId, attr, target) => {
            const variants = { ...this.state.cart[productId].variants };
            for (const property of Object.entries(this.state.cart[productId].variants)) {
              if (property[0] === attr.id) {
                variants[property[0]] = target.value;
                this.setState({ variants });
              }
            }
          },

          handleSelected: (attr, target) => {
            let result;
            for (const propt of Object.entries(this.state.variants)) {
              if (propt[0] === attr.id && this.state.variants[propt[0]] === target.value) {
                result = target.value;
              }
            }
            return result;
          },

          handleSelectedCart: (productId, attr, target) => {
            let result;
            if (this.state.cart[productId]) {
              for (const propt of Object.entries(this.state.cart[productId].variants)) {
                if (propt[0] === attr.id
                    && this.state.cart[productId].variants[propt[0]] === target.value) {
                  result = target.value;
                }
              }
            }
            return result;
          },
        }}
      >
        {this.props.children}
      </MyContext.Provider>
    );
  }

  async componentDidMount() {
    let cart = localStorage.getItem('cart');
    cart = cart ? JSON.parse(cart) : [];
    this.setState({ cart });
  }
}

export default MyProvider;
