import React, { Component } from 'react';
import {
  gql,
  ApolloClient,
  InMemoryCache,
} from '@apollo/client';
import { NavLink } from 'react-router-dom';

const client = new ApolloClient({
  // uri: 'http://localhost:4000/',
  uri: '/graphql',
  cache: new InMemoryCache(),
});

class GetCategory extends Component {
  state = {};

  componentDidMount() {
    client.query({
      query: gql`{
        categories { 
         name   
        }
      }`,
    })
      .then((result) => {
        if (result.loading) {
          console.log(result.loading);
        } else {
          console.log(result.data);
        }
        return result.data;
      })
      .then((data) => this.setState({ data }));
  }

  render () {
    let sevede;

    if (this.state.data) {
      sevede = this.state.data.categories.map((item) => (
        <li key={item.name}>
          <NavLink
            className={({ isActive }) => (isActive ? 'active navigare' : 'navigare')}
            to={`/products/${item.name}`}
          >
            {item.name}
          </NavLink>
        </li>
        ));
    } else {
      sevede = <></>;
    }
    return (sevede);
  }
}

export default GetCategory;
