import React, { Component } from 'react';
import left from '../assets/imgs/left.png';
import right from '../assets/imgs/right.png';

class CarouselProduct extends Component {
  state = {
    currentSlide: 0,
  };

  nextSlide = () => {
    const newSlide = this.state.currentSlide === this.props.gallery.length - 1
      ? 0
      : this.state.currentSlide + 1;
    this.setState({ currentSlide: newSlide });
  };

  prevSlide = () => {
    const newSlide = this.state.currentSlide === 0
      ? this.props.gallery.length - 1
      : this.state.currentSlide - 1;
    this.setState({ currentSlide: newSlide });
  };

  render() {
    const { gallery } = this.props;
    return (
      <>
        <div
          onClick={this.prevSlide}
          className="slide-prev"
          role="button"
          tabIndex="0"
        >
          <img src={left} alt="prev" />
        </div>
        <div>
          { gallery.map((item, index) => (
            <img
              alt=""
              src={item}
              key={index}
              className={index === this.state.currentSlide
                ? 'carousel-show'
                : 'hidden'}
            />
          ))}
        </div>
        <div
          onClick={this.nextSlide}
          className="slide-next"
          role="button"
          tabIndex="0"
        >
          <img src={right} alt="next" />
        </div>
      </>
    );
  }
}

export default CarouselProduct;
