import React, { Component } from 'react';
import { gql, ApolloClient, InMemoryCache } from '@apollo/client';
import CategoryProduct from '../components/CategoryProduct';

const client = new ApolloClient({ uri: 'http://localhost:4000/', cache: new InMemoryCache() });

class GetProducts extends Component {
  state = {
    catName: 'all',
  };

  componentDidMount() {
    const catName = this.state.catName;
    const CatQuery = gql`
        query CategoryPage($catName: String!) {
          category(input: {
            title: $catName,    
          }) {
              products {
                id
                category
                name
                gallery
                inStock
                prices {
                  currency {
                    label
                    symbol
                  }
                  amount
                }
                brand
                attributes {
                  id
                  name
                  type
                  items {
                    displayValue
                    value
                    id
                  }
                }
            }
          }
        }
        `;

    client
      .query({
        query: CatQuery,
        variables: { catName },
      })
      .then((result) => {
        if (result.loading) {
          console.log(result.loading);
        } else {
          console.log(result.data);
        }
        return result.data;
      })
      .then((data) => this.setState({ data }));
  }

  componentDidUpdate() {
    if (this.state.catName !== this.props.category) {
      this.state.catName = this.props.category;
      this.componentDidMount();
    }
  }

  render() {
    let sevede;
    if (this.state.data) {
      sevede = this
        .state
        .data
        .category
        .products
        .map(({
          id,
          category,
          name,
          brand,
          gallery,
          prices,
          inStock,
          attributes,
        }) => (
          <CategoryProduct
            key={id}
            id={id}
            category={category}
            name={name}
            brand={brand}
            gallery={gallery}
            prices={prices}
            inStock={inStock}
            attributes={attributes}
          />
        ));
    } else {
      sevede = <></>;
    }
    return (sevede);
  }
}

export default GetProducts;
