import React, { Component } from 'react';
import GetProducts from '../utils/GetProducts';
import { withParams } from '../utils/Functions';
import MiniCartNou from '../components/MiniCartNou';
import MyContext from '../context/MyContext';

class CategoryPage extends Component {
  static contextType = MyContext;

  render() {
    const { isCartinVisible } = this.context;
    const { params } = this.props;
    const { categoryname } = params;
    return (
      <section className={`cart-on ${isCartinVisible ? 'hidden' : ''}`}>
        <MiniCartNou />
        <div className="container">
          <h1 className="title">{categoryname}</h1>
          <div className="products">
            <GetProducts category={categoryname} />
          </div>
        </div>
      </section>
    );
  }
}

export default withParams(CategoryPage);
