import { Component } from 'react';
import MyContext from '../context/MyContext';

class Currency extends Component {
  static contextType = MyContext;

  componentDidMount() {
    document.addEventListener('mousedown', this.context.handleClickOutside);
    this.setState({
      defaultSelectText: this.context.defaultSelectText,
    });
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.context.handleClickOutside);
  }

  render() {
    const optionsList = this.props.data.currencies;
    const showOptionList = this.context.showOptionList;
    const defaultSelectText = this.context.defaultSelectText;

    return (

      <div className="custom-select-container">
        <div
          className={showOptionList ? 'selected-text active' : 'selected-text'}
          onClick={this.context.handleListDisplay}
          role="button"
          tabIndex={0}
        >
          {defaultSelectText}
        </div>

        {showOptionList && (
        <ul className="select-options">
          { optionsList.map((option) => (
            <li
              className="custom-select-option"
              data-label={option.label}
              data-symbol={option.symbol}
              key={option.label}
              onClick={this.context.handleOptionClick}
            >
              {option.symbol}
              {' '}
              {option.label}
            </li>
          ))}
        </ul>
        )}
      </div>
    );
  }
}

export default Currency;
